@import "../../colors.scss";

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
}

.projectCard {
  min-width: 510px;
  height: 180px;
  background-color: #fff;
  background: linear-gradient(#f8f8f8, #fff);
  box-shadow: 0 8px 16px -8px rgba(0, 0, 0, 0.4);
  border-radius: 3px;
  overflow: hidden;
  position: relative;
}

.projectCard h1 {
  margin: 0;
  text-align: center;
  font-size: 1.2rem;
}
// ==============================bar===================================
.projectCard .bar {
  width: 20px;
  height: 100%;
  background-color: $cardColor;
  float: left;
}

// ==============================GENERAL===================================
.projectCard .general {
  left: 20px;
  width: calc(100% - 20px - 30px - 180px);
  height: 100%;
  float: left;
  z-index: 1;
  box-sizing: border-box;
  padding: 1rem 3rem;
  font-size: 0.8rem;
}

.projectCard .general .generalSubTitle {
  margin: 0;
  font-size: 0.8rem;
  font-weight: 600;
}

.projectCard .general .descriptionContent {
  margin: 0;
  height: 60px;
  overflow-y: hidden; /* Hide vertical scrollbar */
  overflow-x: hidden; /* Hide horizontal scrollbar */
  padding: 0.3rem;
  border-radius: 3px;
  background-color: $cardColor-light;
}

.projectCard .general .projectID {
  position: absolute;
  bottom: 1.5rem;
  font-size: 1em;
}
// ==============================UserProjectCard===================================
.projectCard .user-projectCard {
  left: calc(100% - 180px);
  width: 180px;
  height: 100%;
  float: right;
  border-radius: 0 3px 3px 0;
  background-color: $cardColor;
}

.projectCard .user-projectCard .projectBoard,
.projectCard .user-projectCard .update,
.projectCard .user-projectCard .delete {
  top: 20%;
  position: absolute;
  cursor: pointer;
}

.projectCard .user-projectCard .update {
  top: 40%;
}

.projectCard .user-projectCard .delete {
  top: 60%;
}

.projectCard .user-projectCard .buttonLink {
  width: 180px;
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 1rem;
  padding: 3px 20px 3px 20px;
  display: inline-block;
  border: none;
  transition: all 0.4s ease 0s;
}

.projectCard .user-projectCard .buttonLink:hover {
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
  -webkit-box-shadow: 0px 3px 40px -10px rgba(0, 0, 0, 0.57);
  -moz-box-shadow: 0px 3px 40px -10px rgba(0, 0, 0, 0.57);
  transition: all 0.4s ease 0s;
}
// ==============================showdetail===================================
.projectCard .showDetail {
  width: 30px;
  height: 100%;
  float: right;
  color: #fff;
  cursor: pointer;
  border: none;
  text-align: center;
  outline: none;
  font-size: 15px;

  background: radial-gradient(circle, $cardColor-medium, $cardColor);
  transition: background 1s ease;
}

// ==============================Additional===================================
.projectCard .base {
  position: absolute;
  right: 210px;
  width: 0px;
  height: 100%;
  opacity: 0;
  transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
}

.projectCard .additional {
  background-color: $cardColor;
  width: calc(100% - 20px - 30px - 180px);
  opacity: 1;
  height: 100%;
  position: absolute;
  z-index: 1;
  padding: 1rem 3rem;
  font-size: 0.8rem;
  overflow: hidden;
  display: block;
}

.projectCard .additional h1 {
  color: #fff;
}

.projectCard .additional .more-info .moreInfoSubtitle {
  margin: 0;
  font-size: 0.8rem;
  font-weight: 600;
  color: #fff;
}

.projectCard .additional .more-info .descriptionContent {
  margin: 0 0 0 0;
  font-size: 0.8rem;
  color: #fff;
  height: 60px;
  overflow-y: auto;
  border-radius: 6px;
}
.projectCard .additional .more-info .descriptionContent::-webkit-scrollbar {
  width: 1em;
}

.projectCard
  .additional
  .more-info
  .descriptionContent::-webkit-scrollbar-track {
  border-radius: 1rem;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.projectCard
  .additional
  .more-info
  .descriptionContent::-webkit-scrollbar-thumb {
  border-radius: 1rem;
  background-color: rgb(255, 255, 255);
  outline: 1px solid slategrey;
}
