@import "../colors.scss";

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;

  opacity: 0;
  -webkit-animation: fadeIn ease-in 0.3s forwards;
  -moz-animation: fadeIn ease-in 0.3s forwards;
  animation: fadeIn ease-in 0.3s forwards;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-moz-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.popupInner {
  position: absolute;
  left: 20vw;
  right: 20vw;
  top: 10%;
  bottom: 10%;
  min-width: 300px;
  float: top;
  margin: auto;
  border: 1px solid $navbar;
  border-radius: 3px;
  background: white;
}

// ==============================================================
.popUpTitle {
  line-height: 4rem;
  font-size: 2rem;
  text-align: center;
  padding: 2rem 0;
}
.taskPopUpTitle {
  line-height: 4rem;
  font-size: 2rem;
  text-align: center;
  padding: 1rem 0;
}

.container {
  margin: auto;
}
