@import "../../colors.scss";

.boardTitle {
  margin: 0 1rem;
  min-width: 270px;
}

.projectName {
  font-size: 2rem;
  text-align: left;
}

.description {
  width: 100%;
  height: 100%;
  font-size: 1rem;
  border: 1px dashed rgb(255, 255, 255);
}
.description:hover {
  border: 1px dashed rgb(202, 202, 202);
}
.description:focus {
  outline: none;
  border: 1px dashed rgb(202, 202, 202);
}

.datetitle {
  font-size: 1rem;
  font-weight: light;
}

// =======================================================

.boardTitle .DateRangePicker {
  border: none;
}

// ========================================================

.createTaskBtn {
  color: $createbutton !important;
  background: #ffffff;
  padding: 0.3rem;
  border: 1px solid $createbutton !important;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease 0s;
}
.createTaskBtn:hover {
  color: #ffffff !important;
  border-radius: 50px;
  background: $createbutton !important;
  transition: all 0.5s ease 0s;
  cursor: pointer;
}

.projectDetails {
  font-size: 0.7rem;
}

span + span {
  float: right;
}
