@import "../../colors.scss";

.blank {
  background-image: linear-gradient(
    to left,
    rgba(247, 247, 247, 1),
    rgba(255, 255, 255, 1),
    rgba(247, 247, 247, 1)
  );
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 330px;
}

.landingImg {
  background-image: linear-gradient(
      to right,
      rgba(247, 247, 247, 1),
      rgba(255, 255, 255, 0),
      rgba(255, 255, 255, 0)
    ),
    url("./Landing.jpg");
  background-size: cover;
  display: block;
  width: 100%;
  padding-top: 100%;
}

.landingTitle {
  font-size: 2.5rem;
  text-align: left;
  padding: 0 8rem;
}
.webTitle {
  color: $createbutton;
}
.landingDes {
  font-size: 1rem;
  text-align: left;
  padding: 0 8rem;
}
.btnGroup {
  padding: 0 8rem;
}

.landingBtn {
  color: $createbutton !important;
  background: #ffffff;
  padding: 0.3rem;
  border: 1px solid $createbutton !important;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease 0s;
}
.landingBtn:hover {
  text-decoration: none;
  color: #ffffff !important;
  border-radius: 50px;
  background: $createbutton !important;
  transition: all 0.5s ease 0s;
  cursor: pointer;
}
