@import "../colors.scss";

.subtitle {
  font-weight: 400;
}

.DateRangePicker {
  width: 100%;
  border: 1px solid $createbutton;
}
.DateRangePicker:hover {
  border-color: $createbutton-light;
}

.DateRangePickerInput {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 3px;
}

.DateInput {
  margin: 1px 1px 0.5px 1px;
  width: 50%;
}

.DateInput_input {
  padding: 0.5rem 1rem;
}

.DateRangePicker_picker {
  z-index: 2;
}
// ========================SingleDatePicker===============================
.SingleDatePicker {
  width: 100%;
}

.SingleDatePickerInput {
  width: 100%;
}

.SingleDatePickerInput__withBorder {
  border-radius: 3px;
  border: 1px solid $createbutton;
}

.SingleDatePicker .DateInput {
  width: 99%;
}

.SingleDatePicker .DateInput_input {
  padding: 0.5rem 1rem;
}

//========================================================
.inputBox {
  position: relative;
  height: 3rem;
}
.inputBoxLg {
  position: relative;
  height: 4.5rem;
}

.customInput {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  padding: 0.5rem 1rem;
  border: 1px solid $createbutton;
  border-radius: 3px;
  font-size: 1.2rem;
  color: black;
  outline: none;
}

.customInput:hover {
  border-color: $createbutton-light;
}

.is-invalid,
.is-invalid:hover {
  border-color: rgb(230, 0, 0);
}

.invalid-feedback {
  position: absolute;
  top: 3rem;
}
.invalid-feedback-TA {
  position: absolute;
  top: 5rem;
  color: rgb(230, 0, 0);
  font-size: 0.8rem;
}

.invalid-feedback i {
  margin: 0 0.5rem 0 0;
}

.customInputLabel {
  position: absolute;
  top: 0.5rem;
  left: 0rem;
  font-size: 1.2rem;
  padding: 0 1rem;
  color: gray;
  pointer-events: none;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.customInputLabelActive {
  position: absolute;
  top: -0.8rem;
  left: 0.5rem;
  font-size: 0.8rem;
  padding: 0 0.5rem;
  color: black;
  pointer-events: none;
  background-color: white;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

// ===========================================================================

.submitBtn {
  color: white;
  background-color: $createbutton-medium;
  transition: all 0.4s ease 0s;
}

.submitBtn:hover {
  color: white;
  background-color: $createbutton;
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
  transition: all 0.4s ease 0s;
}
