@import "../../../colors.scss";

#todo {
  background: $todo-card;
}

#inprogress {
  background: $inprogress-card;
}

#done {
  background: $done-card;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
}

.taskCard {
  height: 180px;
  background-color: #fff;
  background: linear-gradient(#f8f8f8, #fff);
  box-shadow: 0 8px 16px -8px rgba(0, 0, 0, 0.4);
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}

.taskCard h1 {
  margin: auto;
  text-align: center;
  font-size: 1.2rem;
  line-height: 2.5rem;
  max-height: 2.5rem;
  overflow: hidden;
}

.taskCard .additional .more-info h1 {
  width: 100%;
  color: #fff;
}

.taskCard .additional {
  position: absolute;
  width: 100px;
  height: 100%;
  transition: width 0.5s;
  overflow: hidden;
  z-index: 1;
}

.taskCard:hover .additional {
  width: 100%;
  border-radius: 0 5px 5px 0;
}

.taskCard .additional .user-taskCard {
  width: 100px;
  height: 100%;
  position: relative;
  float: left;
}

.taskCard .additional .user-taskCard::after {
  content: "";
  display: block;
  position: absolute;
  top: 10%;
  right: -2px;
  height: 80%;
  border-left: 2px solid rgba(0, 0, 0, 0.05);
}

.taskCard .additional .user-taskCard .update,
.taskCard .additional .user-taskCard .delete {
  top: 25%;
  position: absolute;
  cursor: pointer;
}

.taskCard .additional .user-taskCard .delete {
  top: 55%;
}

.taskCard .additional .user-taskCard .buttonLink {
  width: 100px;
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 1rem;
  padding: 5px 20px 5px 20px;
  border-radius: 5px;
  display: inline-block;
  border: none;
  transition: all 0.4s ease 0s;
}

.taskCard .additional .user-taskCard .buttonLink:hover {
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
  -webkit-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  -moz-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  transition: all 0.4s ease 0s;
}

.taskCard .additional .more-info {
  width: calc(100% - 100px);
  float: left;
  position: absolute;
  left: 100px;
  height: 100%;
  opacity: 0;
  padding: 0 1rem;
  transition: opacity 0.4s cubic-bezier(0, 1, 0.2, 1);
  -moz-transition: opacity 0.4s cubic-bezier(0, 1, 0.2, 1);
  -webkit-transition: opacity 0.4s cubic-bezier(0, 1, 0.2, 1);
}
.taskCard:hover .additional .more-info {
  width: calc(100% - 100px);
  float: left;
  position: absolute;
  left: 100px;
  height: 100%;
  opacity: 1;
  transition: opacity 0.55s cubic-bezier(1, 0, 0.8, 0);
  -moz-transition: opacity 0.55s cubic-bezier(1, 0, 0.8, 0);
  -webkit-transition: opacity 0.55s cubic-bezier(1, 0, 0.8, 0);
}

.taskCard .additional .more-info .moreInfoSubtitle {
  margin: 0;
  font-size: 0.8rem;
  font-weight: 600;
  color: #fff;
}

.taskCard .additional .more-info .descriptionContent {
  margin: 0 0 0 0;
  font-size: 0.8rem;
  color: #fff;
  line-height: 1.2rem;
  height: 3.6rem;
  overflow-y: auto;
  border-radius: 6px;
}
.taskCard .additional .more-info .descriptionContent::-webkit-scrollbar {
  width: 1em;
}

.taskCard .additional .more-info .descriptionContent::-webkit-scrollbar-track {
  border-radius: 1rem;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.taskCard .additional .more-info .descriptionContent::-webkit-scrollbar-thumb {
  border-radius: 1rem;
  background-color: rgb(255, 255, 255);
  outline: 1px solid slategrey;
}

.taskCard .additional .coords {
  color: #fff;
  font-size: 0.8rem;
  opacity: 0;
}

.taskCard:hover .additional .coords {
  opacity: 1;
}

.taskCard .additional .coords span + span {
  float: right;
}

.taskCard .general {
  left: 100px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
  box-sizing: border-box;
  padding: 0 1rem 0 1rem;
  font-size: 0.8rem;
}

.taskCard .general .generalSubTitle {
  margin: 0;
  font-size: 0.8rem;
  font-weight: 600;
}

.taskCard .general .descriptionContent {
  margin: 0;
  padding: 0 6px;
  line-height: 1.2rem;
  height: 3.6rem;
  overflow-y: hidden; /* Hide vertical scrollbar */
  overflow-x: hidden; /* Hide horizontal scrollbar */
  border-radius: 6px;
  background-color: rgb(245, 245, 245);
}

.taskCard .general .dueDate {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  font-size: 1em;
}

.taskCard .general .priority {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  font-size: 1em;
}
