@import "../colors.scss";

.createProjectBtn {
  display: flex;
  justify-content: left;
  align-items: center;
  cursor: pointer;
  border-radius: 3px;
  height: 40px;
  outline: none;
  padding: 0 1rem;
  transition: all 0.4s ease;
}
.createProjectBtn svg {
  color: $createbutton;
}
.createProjectBtn span {
  color: $createbutton;
}

.createProjectBtn:hover {
  font-size: 1.1rem;
  transition: all 0.4s ease 0s;
}
